@import url(https://fonts.googleapis.com/css?family=Merriweather:400,900|Six+Caps);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700);
@import url(https://fonts.googleapis.com/css?family=Merriweather:400,900|Six+Caps);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all ease-in-out 250ms;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  color: #606060;
  font-family: "Merriweather", serif;
  line-height: 1.6;
  font-size: 0.875rem;
  background: white;
  overflow-x: hidden;
}

@media (min-width: 50em) {
  body {
    font-size: 1.25em;
  }
}

img {
  max-width: 100%;
}

h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 1em;
}

h1 {
  color: #03afc2;
  line-height: 1;
  font-size: 3.125rem;
}

@media (min-width: 50em) {
  h1 {
    font-size: 4.25rem;
  }
}

h2 {
  font-size: 2rem;
  letter-spacing: 0.15em;
  color: #606060;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
}

@media (min-width: 50em) {
  h2 {
    font-size: 3rem;
  }
}

figure {
  margin: 0;
}

.scroll-top {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  width: 1.5rem;
  padding: 10px;
}

.scroll-top-container {
  border: 1px solid #223843;
  background: #03afc2;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  border-radius: 50%;
  opacity: 0.7;
  display: grid;
  place-items: center;
}

.hide {
  display: none;
}

@media (min-width: 50em) {
  .scroll-top-container {
    display: none;
  }
}

nav {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background: white;

  top: 0;
  right: 0;
  left: 0;
  z-index: 999;

  -webkit-transform: translateX(0);

          transform: translateX(0);
  box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
}

nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  list-style: none;
  background: transparent;
}

.navbar li {
  display: grid;
  place-items: center;
  text-align: center;
  width: 10vw;
}

.navbar li:hover {
  background: rgb(3, 175, 194);
}

.navbar li:hover a {
  color: white;
}

.navbar a {
  font-size: 1.4vw;
  letter-spacing: 0.15em;
  color: #223843;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  text-decoration: none;
  width: 100%;
  padding: 20px 0;
}

@media (max-width: 50em) {
  nav {
    bottom: 0;
    top: auto;
    top: initial;
    border-top: 0.5px solid #223843;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }

  .navbar li {
    width: 25vw;
    border-right: 1px solid rgba(34, 56, 67, 0.3);
  }

  .navbar li:last-of-type {
    border-right: none;
  }

  .navbar a {
    font-size: 3vw;
    font-weight: 900;
  }
}

.intro {
  color: rgba(255, 255, 255, 0.7);
  display: -webkit-flex;
  display: flex;
}

.intro h2 {
  color: #e8e9eb;
  text-transform: capitalize;
}

.intro h2::after {
  content: "";
  width: 20%;
  height: 1px;
  background: rgba(255, 255, 255, 0.5);
  display: block;
  margin: 1em auto 0;
}

@supports (display: grid) {
  .intro {
    display: grid;
    grid-template-columns: minmax(4em, 1fr) minmax(auto, 30em) minmax(4em, 1fr);
  }
}

.intro-middle {
  background: #223843;
  padding: 2.5em 5vw;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.intro-left img,
.intro-right img {
  object-fit: cover;
  min-height: 100%;
  min-width: 100%;
  display: block;
}

.intro-social-icons {
  /* width: 90%; */
  /* max-width: 15em; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 1em auto;
}

.intro-social-icon {
  margin: 0 0.3em;
}

.intro-social-icon img {
  -webkit-filter: invert(30%);
          filter: invert(30%);
}

.intro-social-icon img:hover {
  -webkit-filter: invert(0);
          filter: invert(0);
}

@media (max-width: 50em) {
  .intro-social-icon img {
    width: 70%;
  }
}

.cta {
  text-align: center;
  padding: 5em 0;
  background: #606060;
  color: #e8e9eb;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.cta h2 {
  color: #e8e9eb;
  max-width: 80vw;
}

.cta a {
  text-decoration: none;
  margin-top: 1em;
  background: transparent;
  border: 3px solid #03afc2;
  font-size: 1.5rem;
  letter-spacing: 0.1em;
  color: white;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  padding: 0.5em 1.5em;
  cursor: pointer;
  max-width: 60vw;
}

.cta a:hover {
  background: #03afc2;
  color: white;
}

.cta a:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.welcome {
  background-image: linear-gradient(to top left, #404040, #021823);
  background: #021823;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
}

.welcome h1 {
  font-size: 1.5rem;
  font-weight: 400;
  color: white;
  line-height: 2;
  text-align: center;
  width: 90vw;
}

.welcome h1 span {
  font-size: 2rem;
  letter-spacing: 0.1em;
  color: #03afc2;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 900;
  display: block;
  margin-bottom: 1rem;
}

.welcome h1 p {
  font-size: 0.8rem;
  color: #e8e9eb;
  line-height: 1.8;
  font-family: "Merriweather", serif;
  font-weight: 400;
  display: block;
}

.welcome h1::before,
.welcome h1::after {
  content: "";
  display: block;
  width: 60%;
  height: 1px;
  background: white;
}

.welcome h1::before {
  margin: 0 auto 2em;
}

.welcome h1::after {
  margin: 2em auto 0;
}

.welcome-down {
  position: absolute;
  margin-bottom: 10vw;
  max-width: 3.5vh;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-animation: bounce 2s infinite;
          animation: bounce 2s infinite;
  cursor: pointer;
}

@-webkit-keyframes bounce {
  0% {
    bottom: 5%;
  }
  50% {
    bottom: 8%;
  }
  100% {
    bottom: 5%;
  }
}

@keyframes bounce {
  0% {
    bottom: 5%;
  }
  50% {
    bottom: 8%;
  }
  100% {
    bottom: 5%;
  }
}

.tech {
  text-align: center;
  background: #e8e9eb;
  padding: 5em 0;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.tech h2 {
  max-width: 90vw;
}

.tech p {
  max-width: 70vw;
}

.technologies__items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  width: 80%;
}

@supports (display: grid) {
  .technologies__items {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    margin-top: 20px;
  }
}

.tech__logo {
  width: auto;
  height: auto;
  object-fit: contain;
  -webkit-filter: saturate();
          filter: saturate();
  -webkit-filter: grayscale();
          filter: grayscale();
  margin: 0 5px;
}

@media (min-width: 50em) {
  .welcome h1 {
    font-size: 3vw;
    width: 60vw;
  }

  .welcome h1 span {
    font-size: 3vw;
  }

  .welcome h1 p {
    font-size: 1vw;
  }

  .welcome-down {
    margin-bottom: 0;
  }

  .technologies__items {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
  }

  .tech__logo {
    width: 7vw;
    height: 10vw;
    object-fit: contain;
    -webkit-filter: saturate();
            filter: saturate();
    -webkit-filter: grayscale();
            filter: grayscale();
    margin: 0 5px;
  }
}

.about-me {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  margin-top: 1.5em;
  margin-bottom: 5em;
}

.about-me::after {
  content: "";
  display: block;
  clear: both;
}

.about-img {
  max-height: 25em;
}

.about-title {
  text-shadow: 0 0 0.05em black;
  margin-bottom: 0.25em;
}

.about-subtitle {
  margin: 0.5em 0;
  text-align: left;
}

.about-text,
.about-text a {
  color: #606060;
}

.about-text {
  text-align: left;
}

.about-text a:hover {
  color: #a7a7a7;
}

@supports (display: grid) {
  .about-me {
    display: grid;
    grid-template-columns:
      [port-start] minmax(15em, 1fr) [name-start] minmax(4em, 1fr)
      [img-end] 2fr [port-end];
    grid-column-gap: 0.5em;
  }

  .about-img {
    grid-column: port / img;
    grid-row: 1 / 2;
  }

  @media (min-width: 50em) {
    .about-img {
      grid-row: 1 / 4;
    }
  }

  .about-title {
    grid-column: name / port;
    grid-row: 1 / 2;
    align-self: end;
  }

  @media (min-width: 50em) {
    .about-title {
      text-shadow: 0 0 0.1em black;
    }
  }

  .about-subtitle,
  .about-text {
    grid-column: port / port;
  }

  @media (min-width: 50em) {
    .about-subtitle,
    .about-text {
      grid-column: image-end / port-end;
    }
  }

  .about-subtitle {
    grid-row: 2 / 3;
  }

  .about-text {
    grid-row: 3 / 4;
  }
}

@media (min-width: 42em) {
  .about-me {
    margin-top: 5em;
  }

  .about-img {
    float: left;
    margin-right: 1em;
  }
}

.portfolio {
  text-align: center;
  background: #e8e9eb;
  padding: 1.5em 0;
}

.portfolio-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 90%;
  margin: 0 auto;
  max-width: 75em;
}

@supports (display: grid) {
  .portfolio-items {
    display: grid;
    grid-gap: 0.2em;
    grid-template-columns: repeat(1, 1fr);
  }

  @media (min-width: 50em) {
    .portfolio-items {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@media (min-width: 50em) {
  .portfolio {
    padding: 5em 0;
  }
}

.contact {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  padding: 5em 0;
}

.contact h1 {
  text-align: center;
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

@supports (display: grid) {
  @media (min-width: 50em) {
    form {
      display: grid;
      grid-column-gap: 1em;
      grid-template-areas:
        "name    email"
        "message message"
        "   .    button";
    }

    form .name {
      grid-area: name;
    }

    form .email {
      grid-area: email;
    }

    form .message {
      grid-area: message;
    }

    form .message textarea {
      height: 10em;
    }

    form .button {
      grid-area: button;
    }
  }
}

label {
  display: block;
  margin-top: 1em;
  color: #606060;
}

input,
textarea {
  box-sizing: border-box;
  display: block;
  padding: 0.75em;
  width: 100%;
  background: #e8e9eb;
  border: 1px solid #e8e9eb;
  font-size: 1.2rem;
}

.button {
  display: inline-block;
  width: auto;
  min-width: auto;
  margin-top: 1em;
  margin-left: auto;
  background: transparent;
  border: 3px solid #03afc2;
  font-size: 2rem;
  letter-spacing: 0.15em;
  color: #223843;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  padding: 0.5em 1.5em;
  cursor: pointer;
}

.button:hover {
  background: #03afc2;
  color: white;
}

.button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.success {
  display: grid;
  place-items: center;
}

.success__heading {
  margin-top: 100px;
}

footer {
  background: #223843;
  padding: 3em 0 0.5em;
  text-align: center;
  color: rgba(white, 0.9);
}

.social-icons {
  width: 90%;
  max-width: 15em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 1em auto;
}

.social-icon {
  margin: 0 0.2em;
  width: 25%;
  height: 25%;
}

.social-icon img {
  -webkit-filter: invert(30%);
          filter: invert(30%);
}

.social-icon img:hover {
  -webkit-filter: invert(0);
          filter: invert(0);
}

.copyright {
  color: #e8e9eb;
  font-size: 0.8em;
}

.copyright::before {
  content: "";
  display: block;
  height: 1px;
  background: rgba(232, 233, 235, 0.7);
  margin: 2em auto 1em;
  width: 15%;
}

@media (max-width: 50em) {
  footer {
    margin-bottom: 50px;
  }

